/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { useState, useCallback, useEffect, useRef, useLayoutEffect } from 'react';
import { useSpring, animated } from 'react-spring';
import { Media } from 'src/utils';
import { Color } from 'src/constants';
import AnimatedAngle from 'src/components/AnimatedAngle';

const MainJumbotron = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const [scrollTarget, setScrollTarget] = useState(0);

  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(-30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const bottomArrowAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(-30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 300,
  });

  const scrollToBottom = useCallback(() => {
    window.scroll({ top: scrollTarget, behavior: 'smooth' });
  }, [scrollTarget]);

  const updateScrollTarget = useCallback(() => {
    if (containerRef.current) {
      const scrollTarget = containerRef.current.clientHeight;
      setScrollTarget(scrollTarget);
    }
  }, [setScrollTarget]);

  useLayoutEffect(() => {
    updateScrollTarget();
  }, [updateScrollTarget]);

  useEffect(() => {
    window.addEventListener('resize', updateScrollTarget);
    return () => {
      window.removeEventListener('resize', updateScrollTarget);
    }
  }, [updateScrollTarget]);

  return (
    <div ref={containerRef} css={css`
      position: relative;
      height: 100vh;
    `}>
      <div css={css`
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
      `}>
        <Title style={titleAnimation}>
          Growth<span css={css`color: #ff0000;`}>.</span> And Share
        </Title>
        <Description style={descriptionAnimation}>
          This is <strong>Lubycon</strong>
        </Description>
      </div>
      <div css={css`
        position: absolute;
        bottom: 20%;
        left: 50%;
        transform: translateX(-50%);
      `}>
        <animated.div style={bottomArrowAnimation}>
          <AnimatedAngle onClick={scrollToBottom} />
        </animated.div>
      </div>
    </div>
  );
};

export default MainJumbotron;

const Title = styled(animated.h1)`
  font-style: italic;
  font-size: 3.5rem;
  text-align: center;
  white-space: nowrap;
  ${Media.mobile} {
    font-size: 2rem;
  }
  span {
    color: ${Color.Red500};
  }
`;
const Description = styled(animated.h2)`
  text-align: right;
  font-weight: 200;
  font-size: 1.4rem;
  font-style: italic;
`;
